nav {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	padding: 1vh 10vw;
	position: sticky;
	top: 0;
	z-index: 1;
	background-color: #231942;
}
nav a {
	text-decoration: none;
	color: #7cffa3;
}

.title {
	margin: 0;
	font-size: 2rem;
	font-weight: 500;
	letter-spacing: 0.1rem;
}
.links {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	margin: 0;
	padding: 0;
	letter-spacing: 1px;
	font-size: 0.9rem;
	font-weight: 600;
}
.show {
	display: flex;
}

.links li {
	margin: 0 1.5vw;
	list-style-type: none;
}
.links li:last-child {
	margin-right: 0;
}

.links a {
	padding: 0.3rem 0;
	position: relative;
	transition: ease-in-out 0.3s;
}
.links a:hover::before {
	transform: scaleX(1.1);
}

.icons a {
	padding: 0;
}

.links a::before {
	content: "";
	position: absolute;
	display: block;
	width: 100%;
	height: 3px;
	bottom: 0;
	left: 0;
	background-color: #26ffa0;
	transform: scaleX(0);
	transition: transform 0.3s ease;
}
.links a:hover {
	color: #26ffa0;
}

.icons {
	display: flex;
}

.icons li {
	margin: 0 1vw;
}

nav .menuButton {
	position: absolute;
	right: 1.5vw;
	display: none;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	background: none;
	color: #7cffa3;
}

@media (max-width: 600px) {
	nav {
		flex-direction: column;
		padding: 1vh 5vw;
	}
	.title {
		justify-content: center;
		text-align: center;
	}
	.icons {
		display: flex;
		justify-content: center;
		width: 100%;
		margin-top: 4vh;
	}
	.icons li {
		margin: 0;
	}
	.icons li a {
		margin: 0 3vw;
	}
	.links {
		display: none;
		padding-top: 3vh;
		/* background-color: #d1d1d1; */
	}
	.links li {
		margin: 1vh 0;
	}
	.links li:last-child {
		margin-bottom: 0;
	}
	.links .show li {
		width: 100%;
		text-align: center;
	}
	.show {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		width: 100%;
		height: auto;
	}
	nav .menuButton {
		display: flex;
	}
}
