body {
	margin: 0;
	font-family: "Maven Pro Variable";
}
.App {
	display: flex;
	flex-direction: column;
	background-color: #231942;
}
section {
	display: flex;
	justify-content: center;
	padding: 5em 4em;
}
#home {
	padding: 10em 0;
	padding-bottom: 5em;
	color: #3ddc97;
}
#home h1 {
	margin: 0;
	font-size: 2rem;
	font-weight: 500;
}
.section-title {
	margin-right: 4rem;
	font-size: 1rem;
	font-weight: 600;
	letter-spacing: 0.05rem;
	color: #7cffa3;
	text-align: center;
}
.section-content {
	width: 38vw;
}
#projects .section-content {
	justify-content: center;
}
p {
	margin: 0;
	font-size: 0.95rem;
	color: #bebdc4;
}
a {
	appearance: none;
	text-decoration: none;
	color: #bebdc4;
	font-weight: 600;
	transition: ease-in-out 0.2s;
}

a:hover {
	color: #3ddc97;
	text-shadow: 0px 0px 3px #59d69e;
}

@media (max-width: 600px) {
	section {
		flex-direction: column;
	}
	#home {
		text-align: center;
	}
	.section-title {
		margin: 0;
		margin-bottom: 2rem;
	}
	.section-content {
		width: 100%;
	}
}
