.projectContainer {
	box-sizing: border-box;
	margin: 1rem 0;
	padding: 1.4rem;
	border-radius: 5px;
	transition: ease-in-out 0.03s;
}
.projectContainer:hover {
	box-shadow: #b8b8b858 0px 2px 8px;
}

.projectContainer:first-child {
	margin: 0;
}

.projectHeading {
	display: flex;
	justify-content: space-between;
}
.projectHeading h3 {
	margin: 0;
	letter-spacing: 0.02rem;
	font-weight: 600;
	font-size: 1rem;
	color: #ffffff;
}
.projectHeading a {
	padding: 0.3rem 0;
	position: relative;
	transition: ease-in-out 0.3s;
	text-decoration: none;
	font-size: 0.8rem;
}
.projectHeading a:hover {
	padding: 0.3rem 0;
	position: relative;
	transition: ease-in-out 0.3s;
}
.projectHeading a:hover::before {
	transform: scaleX(1.1);
}
.projectHeading a::before {
	content: "";
	position: absolute;
	display: block;
	width: 100%;
	height: 3px;
	bottom: 0;
	left: 0;
	background-color: #7cffa3;
	transform: scaleX(0);
	transition: transform 0.3s ease;
}
.projectHeading a:hover {
	color: #7cffa3;
}
.projectDescription p {
	margin-top: 4%;
	margin-bottom: 6%;
}

.languageContainer {
	display: flex;
	align-items: flex-end;
	gap: 0.5rem;
}
.language {
	padding: 0.3rem 0.5rem;
	border: 1px solid #7cffa3;
	border-radius: 5px;
	font-size: 0.7rem;
	font-weight: 500;
	color: #7cffa3;
	box-shadow: rgba(0, 0, 0, 0.1) 0px 1px 3px 0px,
		rgba(0, 0, 0, 0.06) 0px 1px 2px 0px;
}

